@-webkit-keyframes bounceInDown {
  from, 60%, 75%, 90%, to {
    -webkit-animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
    animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
  }

  0% {
    opacity: 0;
    -webkit-transform: translate3d(0, -46.875rem, 0);
    transform: translate3d(0, -46.875rem, 0);
  }

  60% {
    opacity: 1;
    -webkit-transform: translate3d(0, 0.390625rem, 0);
    transform: translate3d(0, 0.390625rem, 0);
  }

  75% {
    -webkit-transform: translate3d(0, -0.15625rem, 0);
    transform: translate3d(0, -0.15625rem, 0);
  }

  90% {
    -webkit-transform: translate3d(0, 0.078125rem, 0);
    transform: translate3d(0, 0.078125rem, 0);
  }

  to {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
}

@keyframes bounceInDown {
  from, 60%, 75%, 90%, to {
    -webkit-animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
    animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
  }

  0% {
    opacity: 0;
    -webkit-transform: translate3d(0, -46.875rem, 0);
    transform: translate3d(0, -46.875rem, 0);
  }

  60% {
    opacity: 1;
    -webkit-transform: translate3d(0, 0.390625rem, 0);
    transform: translate3d(0, 0.390625rem, 0);
  }

  75% {
    -webkit-transform: translate3d(0, -0.15625rem, 0);
    transform: translate3d(0, -0.15625rem, 0);
  }

  90% {
    -webkit-transform: translate3d(0, 0.078125rem, 0);
    transform: translate3d(0, 0.078125rem, 0);
  }

  to {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
}

.active .bounceInDown {
  -webkit-animation-name: bounceInDown;
  animation-name: bounceInDown;
}

.timeline-item {
  margin-top: 0.46875rem;
  opacity: 1;
  -webkit-transform: translateY(0);
  transform: translateY(0);
  -webkit-transition: opacity 500ms,-webkit-transform 1000ms;
  transition: opacity 500ms,-webkit-transform 1000ms;
  transition: transform 1000ms,opacity 500ms;
  transition: transform 1000ms,opacity 500ms,-webkit-transform 1000ms;
}

.timeline-item.hide {
  opacity: 0;
  -webkit-transform: translateY(1.5625rem);
  transform: translateY(1.5625rem);
}

@-webkit-keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
